.App {
    position: relative;
    background-color: #efecf3;
    display: inline-block;
    margin: 10%;
    box-shadow: 13px 13px 20px #cbced1, -13px -13px 20px #fff;
    border-radius: 20px;
    padding: 8px;
}

.innerApp {
    position: relative;
    background-color: #f0ecf3;
    display: inline-block;
    border-radius: 17px;
    padding: 20px;
}

#to-do-form input {
    background: linear-gradient(135deg, #f6d365 0%, #fda085 100%);
    border: 0;
    color: #fff;
    width: 200px;
    height: 50px;
    margin: 20px;
    padding: 0 20px;
    font-size: 18px;
    border-radius: 10px;
    margin-bottom: 10px;
    border-radius: 20px;
    box-shadow: inset 2px 2px 2px #adadad, inset -2px -2px 2px #fff;
}

#to-do-form input::-webkit-input-placeholder {
    color: rgba(255, 255, 255, 0.5);
}

#to-do-form input:-ms-input-placeholder {
    color: rgba(255, 255, 255, 0.5);
}

#to-do-form input::placeholder {
    color: rgba(255, 255, 255, 0.5);
}

#to-do-form input:focus {
    outline: none;
    color: #2e2e2e;
}

#toptxt {
    font: bold 15px arial, sans-serif;
    background: linear-gradient(135deg, #c3cfe2 0%, #c3cfe2 100%);
    color: transparent;
    text-shadow: 2px 2px 3px rgba(255, 255, 255, 0.5);
    -webkit-background-clip: text;
    -moz-background-clip: text;
    background-clip: text;
    margin-bottom: -15px;
}

#exctch {
    font: bold 30px arial, sans-serif;
    background-color: #8f8f8f;
    color: transparent;
    text-shadow: 2px 2px 3px rgba(255, 255, 255, 0.5);
    -webkit-background-clip: text;
    -moz-background-clip: text;
    background-clip: text;
}

#submit {
    width: 80px;
    height: 50px;
    margin: 20px;
    margin-bottom: 10px;
    padding: 0 20px;
    border-radius: 20px;
    font-weight: 600;
    font-size: 13px;
    border: 0;
    padding: 0 20px;
    cursor: pointer;
    color: #fff;
    background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
    box-shadow: 1px 1px 3px #b1b1b1, -1px -1px 3px #fff;
    transition: all 0.5s;
}

#submit:hover {
    box-shadow: -2px -2px 5px rgba(255, 255, 255, 0.5), 2px 2px 5px rgba(70, 70, 70, 0.25), inset -2px -2px 5px rgba(255, 255, 255, 0.5), inset 2px 2px 5px rgba(70, 70, 70, 0.25);
}

#to-do-form {
    margin-top: -15px;
}

#first-box {
    -webkit-align-content: flex-start;
            align-content: flex-start;
    display: block;
}
.list {
    color: #fff;
    width: 90%;
    margin: 20px;
    height: auto;
    background: linear-gradient(135deg, #5ee7df 0%, #b490ca 100%);
    box-shadow: 13px 13px 20px #cbced1, -13px -13px 20px #fff;
    border-radius: 15px;
    padding: 0px;
}

.list p {
    padding: 10px;
    position: relative;
    transition: ease-in-out;
}

.list p input {
    background-color: transparent;
    border: 0;
    color: #fff;
    font-size: 18px;
}

.list p input:focus {
    outline: none;
}

.list p span {
    position: absolute;
    right: 10px;
}

.faicons {
    margin: 0 5px;
    font-size: 15px;
    cursor: pointer;
}

.itemsAnim-enter {
    opacity: 0.01;
}

.itemsAnim-enter .itemsAnim-enter-active {
    opacity: 1;
    transition: opacity 300ms ease-in
}

.itemsAnim-leave {
    opacity: 1;
}

.itemsAnim-leave .itemsAnim-leave-active {
    opacity: 0.01;
    transition: opacity 300ms ease-in
}
